

/* card posts */
a.link-card {
  position: relative;
  text-decoration: none;
}
.card-post {
  width: 100%;
}
.link-card .mdl-card__title {
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 150%;
  color: white;
  padding: 0;
  min-height: 200px;
}

.link-card .mdl-card__title h2 {
  font-size: 25px;
  line-height: 28px;
  background-color: rgba(0, 0, 0, 0.4);
  margin: 0;
  text-align: center;
  padding: 5px;
  min-height: 200px;
}

.link-card .mdl-card__title .post-title .post-datetime,
.link-card .mdl-card__title .post-title .post-author {
  font-size: 14px;
  display: block;
}

.link-card:hover .post-title {
  background-color: rgba(0, 0, 0, 0.6);
}

.link-card:hover .post-title,
.post-header,
.post-title,
.mdl-card__title {
  transition: all 200ms ease-in-out;
}
.link-card:hover .mdl-card__title {
  background-size: 180%;
}

