@charset 'UTF-8';


@font-face {
    font-family: 'Social Icons';
    src:    url('../iconfont/SocialIcons.eot?n3n7k9');
    src:    url('../iconfont/SocialIcons.eot?n3n7k9#iefix') format('embedded-opentype'),
        url('../iconfont/SocialIcons.ttf?n3n7k9') format('truetype'),
        url('../iconfont/SocialIcons.woff?n3n7k9') format('woff'),
        url('../iconfont/SocialIcons.svg?n3n7k9#SocialIcons') format('svg');
    font-weight: normal;
    font-style: normal;
}

.social-icons, .social-icons:before {
  font-family: 'Social Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;  /* Preferred icon size */
  display: inline-block;
  width: 1em;
  height: 1em;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  font-feature-settings: 'liga';
}
