.header {
  position: fixed;
  top: -100%;
  left: 0;
  width: 100%;
  display: block;
  min-height: 55px;
  background-color: black;
  z-index: 10;
  transition: all 1000ms ease;
  > .buttons {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: center;
    opacity: 1;
    position: fixed;
    right: -5px;
    top: -5px;
    z-index: 2000;
    padding: 0;
    float: right;
    margin-right: 30px;
    color: white;
    > .menu {
      margin-top:-5px;
    }
  }
}
