/* footer */
.main-footer {
  background: url('../images/bg-parallax.jpg');
  background-position: center top;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-color:#666666;
  border-top: .3rem solid #CCCCCC;
  .social {
    text-align: center;
    margin-top: -50px;
    margin-left: auto;
    margin-right: auto;
    .link {
      margin: 0 2px;
    }
  }
.logo-footer {
  height: 40px;
  width: auto;
  display: block;
  margin: 0 auto 20px auto;
  @media (min-width: 600px){
    display: none;
  }
}

.mdl-mega-footer__bottom-section {
  text-align: center;
  .mdl-mega-footer__link-list li {
    margin-right: 0;
    &::after {
      content: '/';
      margin: 0 3px;
    }
    &:first-child {
      display: none;
    }
    &:last-child {
      &::after {
        content: "";
      }
    }
    @media (min-width: 600px){
      margin-right: 16px;
      font-size: 16px;
      &::after {
        content: "";
        margin: 0;
      }
    }
  }
}
  .mdl-mega-footer__middle-section {
    .social-links {
      display: none;
    }
  }
  @media (max-width: 480px) {
    .social {
      display: none;
    }
    .mdl-mega-footer__middle-section{
      text-align: center;
      .social-links {
        padding: 10px;
        position: relative;
        display: inline-block;
        > .social-icons {
          color: white;
        }
      }
    }
  }
}
