/* imports */
@import "material--colors.min";
@import "material-icons";
@import "social-icons";
@import "animate";
@import "card-home";
@import "fixedHeader";
@import "tables";
/**/

/* font padrao */

*:not(i),
blockquote {
  font-family: "HelveticaNeue", "Helvetica Neue", Helvetica, Arial, sans-serif !important;
}

/**/
/* icons sizes */

.material-icons.md-18,
.social-icons.md-18{font-size: 18px;}
.material-icons.md-24,
.social-icons.md-24{font-size: 24px;}
.material-icons.md-36,
.social-icons.md-36{font-size: 36px;}
.material-icons.md-48,
.social-icons.md-48{font-size: 48px;}


/**/
/* loading da pagina */

@import "loading";

/**/


/* menus flutuante */

.mdl-layout__header .material-icons {
  color: white;
}

.mdl-layout__drawer-button .material-icons{
  color: white;
}

.float-button {
  position: fixed;
  bottom: 5px;
  right: 5px;
  z-index: 1000;
}

.top-header {
  position: fixed;
  opacity: 0;
  color: white;
}
/**/
/* parallax section */

@import "parallax";
/**/

/* bar news e search */

.news-search {
  background: #003366;
  color: white;
  text-align: center;
}

.news-search a{
  color: white;
}
/**/
.mdl-button--fab .social-icons {
  position:absolute;
  top:50%;
  left:50%;
  -webkit-transform:translate(-12px,-12px);
  transform:translate(-12px,-12px);
  line-height:24px;
  width:24px
}


/* footer */

@import "footer";

/**/
.mdl-grid {
  max-width: 960px;
  margin: 0 auto;

}

#top {
  padding-bottom: 100px;
}



/* Larger than tablet */
@media (min-width: 840px) {
  .post-title.destaque {
    font-size: 35px;
    line-height: 30px;
  }
  .float-button {
    bottom: 15px;
    right: 15px;
  }

  .link-card .mdl-card__title h2 {
    padding: 20px;
  }
}

.mdl-tabs__tab-bar {
  overflow-x: auto;
  @media (min-width: 480px){
    overflow-x: hidden;
  }
}

.close {
  position: absolute;
  top: 5px;
  right: 5px;
}

dialog {
  .mdl-grid {
    text-align: center;
  }
  h4 {
    text-align: center;
  }

  .mdl-dialog__content {
    a {
      font-weight: bold;
      display: block;
      margin-bottom: 20px;
      color: white;
      &.facebook,
      &.facebook:hover,
      &.facebook:visited,
      &.facebook:active {
        background-color: #3b5998;
      }
      &.twitter,
      &.twitter:hover,
      &.twitter:visited,
      &.twitter:active {
        background-color: #55acee;
      }
      &.gplus,
      &.gplus:hover,
      &.gplus:visited,
      &.gplus:active {
        background-color: #dd4b39;
      }
      &.whatsapp,
      &.whatsapp:hover,
      &.whatsapp:visited,
      &.whatsapp:active {
        background-color: #4dc247;
        @media (min-width: 600px){
          display: none;
          visibility: hidden;
        }
      }
      i {
        font-size: 20px;
        position: absolute;
        left: 0;
        top: 0;
        background-color: rgba(0,0,0,.1);
        height: 100%;
        line-height: 36px;
        padding: 0 10px;
      }
    }
  }
}


.post-meta {
  background-color: white;
  color: black;
  img {
    height: 50px;
    margin-right: 10px;
    border-radius: 50%;
  }
  a {
    font-weight: bold;
    text-decoration: none;
    color: black;
  }
  button {
    float: right;
  }
  .info {
    display: flex;
    align-items: center;
  }
}

.post-content {
  table {
    @extend %table;
    @extend %table-condensed;
  }

  p {
    font-size: 16px;
    text-indent: 35px;
    text-align: justify;
  }
}
