#parallax {
  .items {
    background-color: rgba(0, 0, 0, 0.4);
  }
  .background{
    position: relative;
    background: url('../images/bg-parallax.jpg') center bottom / 200%;
    background-repeat: no-repeat;
    overflow: hidden;
    z-index: -5;
  }
  .post-title {
    color: white;
    margin-top: 0 !important;
    padding-top: 0 !important;
    margin-bottom: 0 !important;
    padding-bottom: 0 !important;
    h3 {
      padding: 0;
      margin: 0;
      text-align: center;
      font-weight: bold;
    }
  }
  .mdl-grid {
    padding: 5rem 0 5rem 0;
  }
  .mdl-cell {
    text-align: center;
    margin: 5px auto;
  }
  .logo {
    max-width: 20rem;
  }
  .description {
    color: white;
    p {
      margin: 0;
    }
  }
  @media (min-width: 550px) {
    .mdl-grid {
      padding: 8rem 0 8rem 0;
    }
    .background {
      background-size: cover;
    }
  }
  @media (min-width: 840px) {
    .mdl-cell {
      text-align: left;
    }
    .logo {
      max-width: 30rem;
      float: right;
    }
    .mdl-grid {
      padding: 15rem 0 15rem 0;
    }
  }

}
